import React, { useState } from 'react';
import StarRating from './StarRating';
import { Review } from '../data/reviews';

interface ReviewItemProps {
  review: Review;
  onHelpfulClick?: (reviewId: string, isHelpful: boolean) => void;
}

const ReviewItem: React.FC<ReviewItemProps> = ({ review, onHelpfulClick }) => {
  const [showFullReview, setShowFullReview] = useState(false);
  const [userVote, setUserVote] = useState<'helpful' | 'not-helpful' | null>(null);
  
  // Truncate long reviews
  const shouldTruncate = review.comment.length > 200;
  const displayComment = showFullReview || !shouldTruncate 
    ? review.comment 
    : review.comment.substring(0, 200) + '...';

  const formatDate = (date: Date) => {
    return date.toLocaleDateString('en-US', { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    });
  };

  const handleHelpfulClick = (isHelpful: boolean) => {
    if (userVote === null) {
      setUserVote(isHelpful ? 'helpful' : 'not-helpful');
      onHelpfulClick?.(review.id, isHelpful);
    }
  };

  return (
    <div className="border-b border-gray-200 pb-6 mb-6 last:border-b-0">
      {/* User Info Header */}
      <div className="flex items-start gap-3 mb-3">
        <div className="w-8 h-8 bg-gray-300 rounded-full flex items-center justify-center text-sm font-semibold text-gray-600">
          {review.userName.charAt(0)}
        </div>
        <div className="flex-1">
          <div className="flex items-center gap-2 mb-1">
            <span className="font-medium text-gray-900">{review.userName}</span>
            {review.topReviewer && (
              <span className="px-2 py-0.5 bg-orange-100 text-orange-800 text-xs rounded-full">
                Top Reviewer
              </span>
            )}
            {review.vineCustomer && (
              <span className="px-2 py-0.5 bg-green-100 text-green-800 text-xs rounded-full">
                Vine Customer
              </span>
            )}
            {review.earlyReviewer && (
              <span className="px-2 py-0.5 bg-blue-100 text-blue-800 text-xs rounded-full">
                Early Reviewer
              </span>
            )}
          </div>
          <div className="flex items-center gap-2 mb-2">
            <StarRating rating={review.rating} size="sm" />
            <span className="text-sm font-semibold text-gray-900">{review.title}</span>
          </div>
          <div className="flex items-center gap-3 text-xs text-gray-500">
            <span>Reviewed on {formatDate(review.dateCreated)}</span>
            {review.verified && (
              <span className="flex items-center gap-1 text-orange-600">
                <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
                Verified Purchase
              </span>
            )}
          </div>
        </div>
      </div>

      {/* Review Content */}
      <div className="ml-11">
        <p className="text-gray-700 mb-3 leading-relaxed">
          {displayComment}
        </p>
        
        {shouldTruncate && (
          <button
            onClick={() => setShowFullReview(!showFullReview)}
            className="text-blue-600 hover:text-blue-800 text-sm font-medium mb-3"
          >
            {showFullReview ? 'Show less' : 'Read more'}
          </button>
        )}

        {/* Review Images */}
        {review.images && review.images.length > 0 && (
          <div className="flex gap-2 mb-4">
            {review.images.map((image, index) => (
              <div
                key={index}
                className="w-16 h-16 bg-gray-200 rounded border cursor-pointer hover:opacity-80 transition-opacity"
                style={{ 
                  backgroundImage: `url(${image})`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center'
                }}
              />
            ))}
          </div>
        )}

        {/* Helpful Section */}
        <div className="flex items-center gap-4 text-sm">
          <span className="text-gray-600">Helpful?</span>
          <div className="flex gap-2">
            <button
              onClick={() => handleHelpfulClick(true)}
              disabled={userVote !== null}
              className={`px-3 py-1 border rounded-full text-sm transition-colors ${
                userVote === 'helpful'
                  ? 'bg-blue-50 border-blue-300 text-blue-700'
                  : userVote === null
                  ? 'border-gray-300 text-gray-700 hover:bg-gray-50'
                  : 'border-gray-200 text-gray-400 cursor-not-allowed'
              }`}
            >
              Yes ({review.helpful + (userVote === 'helpful' ? 1 : 0)})
            </button>
            <button
              onClick={() => handleHelpfulClick(false)}
              disabled={userVote !== null}
              className={`px-3 py-1 border rounded-full text-sm transition-colors ${
                userVote === 'not-helpful'
                  ? 'bg-red-50 border-red-300 text-red-700'
                  : userVote === null
                  ? 'border-gray-300 text-gray-700 hover:bg-gray-50'
                  : 'border-gray-200 text-gray-400 cursor-not-allowed'
              }`}
            >
              No ({review.notHelpful + (userVote === 'not-helpful' ? 1 : 0)})
            </button>
          </div>
          {userVote && (
            <span className="text-green-600 text-xs">Thank you for your feedback!</span>
          )}
        </div>
      </div>
    </div>
  );
};

export default ReviewItem;
